import { Component, OnInit } from '@angular/core';
import { ZaposleniService } from './zaposleni.service';
import { Router, ActivatedRoute } from '@angular/router';

@Component({
  selector: 'app-zaposleni',
  templateUrl: './zaposleni.component.html',
  styleUrls: ['./zaposleni.component.css']
})
export class ZaposleniComponent implements OnInit {

  private zaposleni: {ime: string, prezime: string, email: string }[] = [];

   constructor(private zaposleniService: ZaposleniService, private ruter: Router, private ruta: ActivatedRoute) { }

    dodajZaposlenog(ime: string, prezime: string, email: string) {
      this.zaposleniService.dodajZaposlenog(ime, prezime, email);
    }
    ispisiDetalje(ime: string, prezime: string, email: string) {
      alert('Ime: ' + ime + '\nPrezime: ' + prezime + '\nEmail: ' + email);
      return false;
    }

    ngOnInit() {
      this.zaposleni = this.zaposleniService.zaposleni;
    }
     ucitaj() {
       this.ruter.navigate(['zaposleni'], { relativeTo: this.ruta});
     }
}
